import React, { useState } from "react";
import { Layout, Menu } from "antd";
import {
  LineChartOutlined,
  PieChartOutlined,
  SlidersOutlined,
  FileOutlined,
  DollarOutlined,
  ExperimentOutlined,
  DotChartOutlined,
} from "@ant-design/icons";

import { Link } from "react-router-dom";

const { Sider } = Layout;

const items = [
  {
    key: "assets",
    icon: <PieChartOutlined />,
    label: "Assets",
    children: [
      {
        key: "equities",
        icon: <SlidersOutlined />,
        label: <Link to="assets/equities">Equities</Link>,
      },
      {
        key: "fixed_income",
        icon: <FileOutlined />,
        label: <Link to="assets/fixed-income">Fixed Income</Link>,
      },
      {
        key: "commodities",
        icon: <DollarOutlined />,
        label: <Link to="assets/commodities">Commodities</Link>,
      },
    ],
  },
  {
    key: "Strategy",
    icon: <LineChartOutlined />,
    label: <Link to="analytics">Strategy</Link>,
  },
  {
    key: "Research",
    icon: <ExperimentOutlined />,
    label: <Link>Research</Link>,
    children: [
      {
        key: "modelv1",
        icon: <DotChartOutlined />,
        label: <Link to="/trading-algorithm-progress/models/v1">Model V1</Link>,
      },
      {
        key: "modelv2",
        icon: <DotChartOutlined />,
        label: <Link to="/trading-algorithm-progress/models/v2">Model V2</Link>,
      },
    ],
  },
];

export const Sidebar = () => {
  const [collapsed, setCollapsed] = useState(false);

  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };
  return (
    <Sider
      theme="light"
      width={200}
      collapsible
      collapsed={collapsed}
      onCollapse={toggleCollapsed}
    >
      <div className="demo-logo-vertical" />
      <Menu mode="inline" items={items} />
    </Sider>
  );
};
