import { Layout, Card, Tag, Typography, Row, Col } from "antd";
const { Content } = Layout;
const { Title } = Typography;

export const Home = () => {
  return (
    <Content
      style={{
        padding: 24,
        margin: 0,
        minHeight: 280,
      }}
    >
      <Card title="Info">
        <Row>
          <Col span={2}>
            <Title level={5} style={{ marginTop: "0px" }}>
              Assets List
            </Title>
          </Col>
          <Col>
            <Tag color="volcano" style={{ marginTop: "4px" }}>
              Not Started
            </Tag>
          </Col>
        </Row>
        <Row>
          <Col span={2}>
            <Title level={5} style={{ marginTop: "0px" }}>Strategy</Title>
          </Col>
          <Col>
            <Tag color="volcano" style={{ marginTop: "4px" }}>Not Started</Tag>
          </Col>
        </Row>
        <Row>
          <Col span={2}>
            <Title level={5} style={{ marginTop: "0px" }}>Research</Title>
          </Col>
          <Col>
            <Tag color="blue" style={{ marginTop: "4px" }}>On Progress</Tag>
          </Col>
        </Row>
      </Card>
    </Content>
  );
};
